.header {
  background-color: mediumseagreen;
  width: 100vw;
  height: 10vh;
  position: fixed;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
  border: black 1px solid;
  .header-content {
    width: 100%;
    border: black 1px solid;
    display: flex;
    flex-direction: row;
    .brand-logo {
      width: 20vw;
      border: black 1px solid;
      display: flex;
      flex-direction: row;
      // flex-grow: 1;
    }
    .information {
      border: black 1px solid;
      flex-grow: 6;
    }
    .profile {
      border: black 1px solid;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      flex-grow: 1;
