.header {
  background-color: mediumseagreen;
  width: 100vw;
  height: 10vh;
  position: fixed;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
  border: black 1px solid;
}
.header .header-content {
  width: 100%;
  border: black 1px solid;
  display: flex;
  flex-direction: row;
}
.header .header-content .brand-logo {
  width: 20vw;
  border: black 1px solid;
  display: flex;
  flex-direction: row;
}
.header .header-content .information {
  border: black 1px solid;
  flex-grow: 6;
}
.header .header-content .profile {
  border: black 1px solid;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}/*# sourceMappingURL=style.css.map */